.content-container {
  position: relative;
  display: flex;
  flex-direction: column;
}

.content-container svg {
  position: absolute;
  z-index: 8;
  pointer-events: none;
}

.content-container .content {
  position: relative;
  z-index: 10;
  height: 2000px;
  padding: 0 150px;
  background-color: rgb(39, 48, 54);
  background-image: url(../../images/dots-grey.svg);
  background-repeat: repeat;
}
